.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  gap: 50px;
  padding: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ResultContainer {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}
.InputContainer {
  max-width: 100%;
}
.InputTextarea {
  max-width: 100%;
}
